import { Box, Grid } from '@mui/material'
import React from 'react'
import Picture from './Picture'
import Sidebar from './Sidebar'

function Home({numOfImages}) {
    console.log("herexx")
    console.log(numOfImages)
  return (
    <Box flex={4}>

        <Grid container spacing={4}>{

            [...Array(numOfImages)].map((x, index) => 
            <Grid key={Math.random(2000)} item xs={4} >
            <Picture index={index} /> 
            </Grid> 
            )}

        </Grid> 
    </Box>
  )
}

export default Home