import  Home  from './component/Home';
import { Box, createTheme, Grid, ThemeProvider } from '@mui/material';
import { useState } from 'react';
import './App.css';
import NavBar from './component/NavBar';
import Picture from './component/Picture';
import { Route, Routes } from 'react-router-dom';
import About from './component/About';
import Sidebar from './component/Sidebar';
import { Stack } from '@mui/system';

function App() {

 
  const [numOfImages, setNumOfImages] = useState(20);
  const [mode, setMode] = useState("light");

  const darkTheme = createTheme({
    palette: {
      mode: mode,
    },
  });
  console.log("here")


  return (
    <ThemeProvider theme={darkTheme}>

    <Box className="App" bgcolor={"background.default"} color={"text.primary"}>
      
      <NavBar numOfImages={numOfImages}/>
      
     

        <Stack direction="row">
          <Sidebar mode={mode} setMode={setMode} />

          <Routes>
            <Route index element={<Home numOfImages={numOfImages}/>} />
            <Route path="about" element={<About/>}/>
            <Route path="picture-space" element={<Home  numOfImages={numOfImages}/>} />
          </Routes>
        </Stack>
    </Box>
    </ThemeProvider>

  );
}

export default App;
