import {createSlice} from "@reduxjs/toolkit";


const initialState = {
        imgURL:[],
        imgDescription:[],
        imgPhotographer: [],
        photographerURL: []
}

export const imgInfo = createSlice({
    name:"image", 
    initialState,

    reducers:{
        setImgURL: (state, action) =>{
            state.imgURL.push(action.payload)
        },
        setImgDescription: (state, action) => {
            state.imgDescription.push(action.payload)
        },
        setImgPhotographer: (state, action) =>{
            state.imgPhotographer.push(action.payload)
        },
        setPhotographerURL: (state, action)=>{
            state.photographerURL.push(action.payload)
        },
        clearState: (state) =>  {
            state.imgURL.length = 0
            state.imgDescription.length = 0;
            state.imgPhotographer.length = 0;
        }


    }

})

export const {setImgURL, setImgDescription, setImgPhotographer, clearState, setPhotographerURL} = imgInfo.actions;

