import react, {useEffect, useState, useRef} from "react";

import { styled } from '@mui/material/styles';
import Card from '@mui/material/Card';
import CardHeader from '@mui/material/CardHeader';
import CardMedia from '@mui/material/CardMedia';
import CardContent from '@mui/material/CardContent';
import CardActions from '@mui/material/CardActions';
import Collapse from '@mui/material/Collapse';
import Avatar from '@mui/material/Avatar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import { red } from '@mui/material/colors';
import FavoriteIcon from '@mui/icons-material/Favorite';
import ShareIcon from '@mui/icons-material/Share';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import { useDispatch, useSelector } from 'react-redux';
import { createClient } from 'pexels';
import { Checkbox, Grid } from "@mui/material";
import { Favorite, FavoriteBorder } from "@mui/icons-material";
import { Link } from "react-router-dom";

const ExpandMore = styled((props) => {
  const { expand, ...other } = props;
  return <IconButton {...other} />;
})(({ theme, expand }) => ({
  transform: !expand ? 'rotate(0deg)' : 'rotate(180deg)',
  marginLeft: 'auto',
  transition: theme.transitions.create('transform', {
    duration: theme.transitions.duration.shortest,
  }),
}));

export default function Picture({index}) {
  
  
  const [image, setImage] = useState([]);


  const {imgURL, imgDescription, imgPhotographer, photographerURL } = useSelector(state => state.image)

  console.log("phtooh", imgPhotographer.length == 0)

  const dispatch = useDispatch()

  function shortenDescription(description){
    description = description.split(" ");
    description = description.slice(0, 5);
    description = description.join(" ");

    return description;
  }

  function shareImage(url, photographer, description){
    console.log("event")
    if(navigator.share){

      navigator.share({
        text:`Hey, check out this cool photo of a ${description} taken by ${photographer}`,
        url:url,
        title: "cool photo"
      }).then(() => console.log("Content shared succefully"))
    }else{
      navigator.clipboard.writeText("share api not supported on this browser")
    }
      
  }

  
  //generate random date between two given dates as show under. 
  function randomDate(start, end) {
    return new Date(start.getTime() + Math.random() * (end.getTime() - start.getTime())); //function to generate random dates. 
  }

    const d = randomDate(new Date(2017, 0, 1), new Date());
  

  
  const [expanded, setExpanded] = useState(false);

  const handleExpandClick = () => {
    setExpanded(!expanded);
  };

  return (
    <Card flex={3} sx={{ maxWidth: 500, ml:3, mt:3 }}>
      <CardHeader
        avatar={
          <a target="_blank" underline='none' style={{underline:"none", color:"white", textDecoration:"none"}} href={photographerURL[index]}>
          <Avatar sx={{ bgcolor: red[500], cursor:"pointer" }} aria-label="recipe">
            {imgPhotographer.length != 0 ? imgPhotographer[index][0].toUpperCase() : ""}
          </Avatar>
          </a>
        }
        action={
          <IconButton aria-label="settings">
            <MoreVertIcon />
          </IconButton>
        }
        title={imgPhotographer[index]}
        // title={photographerURL[index]}
        subheader={d.toDateString()}
      />
     

        <CardMedia


          component="img"
          height="400"
          image={imgURL[index]} 
          alt={"photo by " + imgPhotographer}
        >
            
        </CardMedia>


  

      <CardContent>
        <Typography variant="body2" color="text.secondary">
         {


            imgDescription.length != 0 ? shortenDescription(imgDescription[index]) : ""
         
         
         }
        </Typography>
      </CardContent>
      <CardActions disableSpacing>
        <IconButton aria-label="add to favorites">
        <Checkbox  icon={<FavoriteBorder />} checkedIcon={<Favorite color="error"/>} />

        </IconButton>
        <IconButton onClick={() => shareImage(imgURL[index], imgPhotographer[index], imgDescription[index])} aria-label="share">
          <ShareIcon />
        </IconButton>
        <ExpandMore
          expand={expanded}
          onClick={handleExpandClick}
          aria-expanded={expanded}
          aria-label="show more"
        >
          <ExpandMoreIcon />
        </ExpandMore>
      </CardActions>
      <Collapse in={expanded} timeout="auto" unmountOnExit>
        <CardContent>
          <Typography paragraph>Description:</Typography>
          <Typography paragraph>
            {imgDescription[index]}
          </Typography>
      


        </CardContent>
      </Collapse>
    </Card>
  );
}