import react, { useEffect, useState } from 'react'
import { styled, alpha } from '@mui/material/styles';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import InputBase from '@mui/material/InputBase';
import { Link, Menu, MenuItem } from '@mui/material';
import AdUnitsIcon from '@mui/icons-material/AdUnits';
import SearchIcon from '@mui/icons-material/Search';
import MenuIcon from '@mui/icons-material/Menu';
import { color } from '@mui/system';
import { useDispatch } from 'react-redux';
import { clearState, setImgDescription, setImgPhotographer, setImgURL, setPhotographerURL, setQuery, setURL } from '../Redux/query';
import { createClient } from 'pexels';
import App from '../App';
import { NavLink, Route, Routes } from 'react-router-dom';
import Picture from './Picture';
import Home from "./Home"


const pages = {
  Home: "/",
  About: "/about",
  Contact: "www.toContact",
  Github: "https://github.com/Muath01/picture-space"
} 



  const Search = styled("div")(({theme}) => ({
    backgroundColor:"white",
    backgroundColor:"white",
    padding:"0 10px",
    borderRadius: "5px",
    width:"40%"
  
  }))



export default function NavBar({numOfImages}) {
    const [anchorElNav, setAnchorElNav] = useState(null);
    const [search, setSearch] = useState("");
    const [query, setQuery] = useState("random");
    const client = createClient(process.env.REACT_APP_API_KEY);


    const [reply, setReply] = useState("");
    const [picInfo, setPicInfo] = useState("");
    const [photographer, setPhotographer] = useState([]);
    


    const dispatch = useDispatch()


    async function getImages(){
      
      
      let response;

      try{

        response = await client.photos.search({ query, per_page: numOfImages })

        if(response.error) {
          const error = await response.error((err) => console.log("err", err))
        }
      } catch(error){
        console.log("ERRRORRR", error)

      }

      if(response){

        
        response.photos.forEach(photo => {
        dispatch(setImgURL(photo.src.original))
        dispatch(setImgPhotographer(photo.photographer))
        dispatch(setImgDescription(photo.alt))
        dispatch(setPhotographerURL(photo.photographer_url))
      })
      
   
      
      
      setReply(response)
      console.log(response)
    }



    }

    useEffect(() => {
      setQuery("");
      getImages();
    }, [])

    // useEffect(() => {
    //   console.log("photographer", reply)
      
    // }, [photographer])

    function handleKeyPress(e){
      if(e.key == "Enter"){
        dispatch(clearState())
        getImages();
        // console.log("here", reply)
        // console.log("picInfo", picInfo)
        setSearch("");
      }
    }

   

  return (
    

      <AppBar position="sticky" sx={{backgroundColor:"#455a64", m:0, p:0}}>
        
        <Toolbar textalign="right" sx={{display:"flex", justifyContent:"end", p:0, m:0}}>
        <Search sx={{position:"absolute", left:"2em"}}>
  
            <InputBase
            sx={{pr:20, color:"black" }}
            value={query}
            onChange={(e) => setQuery(e.target.value)}
            placeholder="Search"
            onKeyDown={handleKeyPress}
            >
            </InputBase>
          </Search>

            {Object.keys(pages).map((page, index) => (
              <MenuItem sx={{color:"white", textDecoration:"none"}} key={page} >{
                page=="Github" ? (
                  <a sx={{color:"white", backgroundColor:"red"}} className="Nav" target="_blank" underline='none' href={pages[page]}  textalign="left">{page}</a>
                  ) :
                  <NavLink  className="Nav" underline='none' to={pages[page]} sx={{color:"white"}} textalign="left">{page}</NavLink>
                  
                }
              
            </MenuItem>
            ))}
        </Toolbar>

      </AppBar>

   
  );
}

